.footer {
  width: 100%;
  background-color: #070749;
  padding-right: rem(20);
  padding-left: rem(20);
  color: #fff;
  margin-bottom: rem(30);
  &__inner {
    width: 100%;
    max-width: rem(832);
    margin-right: auto;
    margin-left: auto;
    padding-top: rem(105);
    padding-bottom: rem(72);
  }
  &__logos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: rem(25);
    border-bottom: 1px solid #fff;
    @include mq(sp) {
      flex-direction: column;
    }
  }
  &__lancer {
    display: block;
    width: rem(283);
    height: rem(30);
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    @include mq(sp) {
      width: rem(189);
      height: rem(20);
      margin-left: 0;
      margin-right: auto;
    }
  }
  &__digital {
    display: flex;
    align-items: center;
    @include mq(sp) {
      margin-top: rem(25);
    }
  }
  &__digitalName {
    font-size: rem(14);
    font-weight: 400;
    margin-right: rem(23);
    @include mq(sp) {
      font-size: rem(12);
    }
  }
  &__digitalLogo {
    width: rem(205);
    height: auto;
    img {
      width: 100%;
      height: 100%;
    }
    @include mq(sp) {
      width: rem(140);
    }
  }
  &__contents {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: rem(25);
    @include mq(sp) {
      flex-direction: column;
    } 
  }
  &__navList {
    display: flex;
    flex-wrap: wrap;
    width: rem(450);
    margin-bottom: rem(-15);
    @include mq(sp) {
      width: 100%;
      margin-right: auto;
      margin-left: auto;
    } 
  }
  &__navItem {
    font-size: rem(12);
    width: rem(150);
    margin-bottom: rem(15);
    a {
      display: block;
      width: 100%;
    }
    @include mq(sp) {
      font-size: rem(11);
      width: 33.333%;
    } 
  }
  &__relatedLogos {
    width: rem(310);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    @include mq(sp) {
      width: 100%;
      margin-top: rem(25);
    }
    padding-bottom: rem(-15);
  }
  &__relatedLogo {
    a {
      display: flex;
      align-items: center;
    }
    &.one {
      width: 10%;
    }
    &.two {
      width: 30%;
    }
    &.three {
      width: 40%;
    }
    margin-right: 10%;
    margin-bottom: rem(15);
    img {
      width: auto;
      height: auto;
      object-position: center;
    }
    &:nth-of-type(3n) {
      margin-right: 0;
    }
    &.academy {
      img {
        width: 70%;
        height: auto;
      }
    }
  }
  &__information {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: rem(72);
  }
  &__sns {
    display: flex;
  }
  &__snsLink {
    width: rem(20);
    height: 100%;
    margin-right: rem(30);
    img {
      width: 100%;
      height: auto;
    }
    &--facebook {
      margin-top: rem(-2);
    }
  }
  &__copyright {
    font-size: rem(10);
    font-weight: 400;
  }
}